/* GLOBAL */

body {
  margin: 0;
  background-color: white;
  font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", 
  Helvetica, Arial, "Lucida Grande", sans-serif;
}

aside {
  position:fixed;
  width: 250px;
  height: 100%;
  top: 0;
  left: 0;
}

aside ul {
  list-style: none;
  font-size: 1.2em;
  font-weight: bold;
  margin-top: 40px;
  margin-bottom: 40px;
}

aside li {
  margin-bottom: 15px;
}

#nav-link {
  text-transform:uppercase;
  text-decoration: none;
  color: black
}

#nav-link.active {
  text-decoration: none;
  color: #403681;
}

#nav-link:hover {
  color: #403681;
}

#logo {
  font-size: 2em;
  letter-spacing: -0.05em;
  text-decoration: none;
  color: black;
  margin-left: -7px;
}

#logo:hover {
  color: #403681;
}

#social-icons {
  width: 75%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-around;
}

.fa {
  padding: 8px;
  width: 18px;
  text-align: center;
  text-decoration: none;
  border-radius: 50%;
  background-color: black;
  color: white;
}

#content {
  margin-left: 250px;
  width: 700px;
  padding-top: 45px;
}

/* HOME PAGE */

.announce-title {
  background-color: #403681;
  color: white;
  font-size: 1.8em;
  text-align: center;
  text-transform: uppercase;
  margin-top: 0;
}

.announce-body {
  display: flex;
}

.announce-body-one {
  width: 60%;
}

.announce-art {
  width: 420px;
}

.announce-body-two {
  display: flex;
  flex-direction: column;
}

.announce-notes {
  margin-top: 0;
  padding-left: 15px;
  text-align: justify;
  text-justify: distribute;
}

.announce-buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.announce-button {
  background-color: black;
  color: white;
  font-size: 1.1em;
  font-weight: bold;
  padding: 10px;
  margin: 15px;
  text-decoration: none;
  text-align: center;
}

/* MUSIC */

.release {
  display: flex;
  flex-direction: row;
  margin-bottom: 30px;
}

.release-art {
  width: 50%;
}

.release-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.release-title {
  font-size: 1.2em;
  margin-top: 0;
  margin-bottom: 5px;
  margin-left: 30px;

}

.release-date {
  margin-top: 0;
  margin-left: 30px;
}

.release-notes {
  font-family: monospace;
  font-size: 0.7em;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 30px;
}

.release-buttons {
  margin-top: 20px;
  margin-left: 30px;
  display: flex;
}

.release-button {
  background-color: black;
  color: white;
  font-weight: bold;
  padding: 10px;
  text-decoration: none;
  margin-right: 10px;

}

.release-button:hover {
  background-color: #404040;
  color: white;
  padding: 10px;
  text-decoration: none;
}

/* VIDEOS */

.youtube-video {
  margin-bottom: 30px;
}

/* PRESS SHOTS */

.press-shot {
  width: 100%;
  margin-bottom: 0;
}

.press-shot-caption {
  margin-top: 0;
  margin-bottom: 30px;
  text-align: right;
  font-family: monospace;
}

/* CONTACT */

#mc_embed_signup {
  font:14px Helvetica,Arial,sans-serif;
  background-color: lightgray;
  padding: 20px;
  width: 50%;
}

.email-address {
  color: #403681;
}

.contact-blurb {
  margin-top: 0;
}

/* SHOWS */

.shows-blurb {
  margin-top: 0;
}

.shows {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.show {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: monospace;
}

.shows-blurb {
  text-decoration: none;
  color: black;
}

.shows-blurb:hover {
  color: #403681;
}

.show-button {
  background-color: black;
  color: white;
  font-weight: bold;
  padding: 10px;
  text-decoration: none;
  margin-left: 10px;
}

.show-button:hover {
  background-color: #404040;
  color: white;
  padding: 10px;
  text-decoration: none;
  margin-left: 10px;
}

.show-location {
  margin-left: 50px;
}

.show-geo {
  margin-top: 0;
}

.show-venue {
  margin-bottom: 0;
}

/* LYRICS */

.song p {
  margin: 0;
}

.song h3 {
  margin-bottom: 5px;
}

.song {
  margin-bottom: 30px;
}

/* BIO */

.elevator-pitch {
  display: flex;
  flex-direction: row;
}

.bio-image {
  width: 100%;
}

.elevator-pitch-text {
  margin-top: 0;
  margin-left: 30px;
  text-align: justify;
  text-justify: inter-word;
}

.bio-text {
  text-align: justify;
  text-justify: inter-word;
}

